/**
 * MultiCompareInner.js
 * Multi Compare view with different insurers
 * Contains the main policy type tab view logic
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Row } from 'reactstrap';

import TabView from '../../Templates/TabView/TabView';
import CompareView from '../../Templates/CompareView/CompareView';
import ChangeInitialFormData from '../../Templates/ChangeInitialFormData/ChangeInitialFormData';

import Enquiry from '../../Templates/Enquiry/Enquiry';

import { setPlanType, setSelectedIndex } from '../../../actions/compareActions';

// The default tabs array
const resultToMatch = ['basic', 'essential', 'elite', 'default'];

class MultiCompareInner extends Component {
  /**
   * Lifecycle Method - getDerivedStateFromProps
   */
  static getDerivedStateFromProps(nextProps, prevState) {
    // Goes here first before did mount
    // Get new policy available policy types
    if (nextProps.data.policyTypes !== prevState.policyTypes) {
      return ({
        policyTypes: nextProps.data.policyTypes,
      });
    }

    return null;
  }

  /**
   * Lifecycle Method - constructor
   */
  constructor(props) {
    super(props);

    const { data } = this.props;
    // tabIndex - The current tab index in effect
    // currentTab - The name of the current tab
    // tabs - The tabs array data
    // policyTypes - The possible policy types to display
    this.state = {
      tabIndex: 0,
      currentTab: null,
      tabs: null,
      policyTypes: data.policyTypes,
    };

    this.handleTabClick = this.handleTabClick.bind(this);
    this.renderTabs = this.renderTabs.bind(this);
  }

  /**
   * Lifecycle Method - componentDidMount
   */
  componentDidMount() {
    // Render inital tabs
    const tabs = this.renderTabs(null, true);
    // Set initial plan type
    this.props.setPlanType(_.lowerCase(tabs.currentTab));
  }

  /**
   * Lifecycle Method - shouldComponentUpdate
   */
  shouldComponentUpdate(nextProps, nextState) {
    const { policyTypes, tabs, currentTab } = this.state;
    const { data } = this.props;

    // Render new tabs if policy types are changed
    if (!_.isEqual(policyTypes, nextState.policyTypes)) {
      this.renderTabs(nextState.policyTypes);
    }

    // Update only when tabs or data are updated
    return tabs !== nextState.tabs ||
          currentTab !== nextState.currentTab ||
          data !== nextProps.data;
  }

  /**
   * Lifecycle Method - componentDidUpdate
   */
  componentDidUpdate(prevProps, prevState) {
    const { tabs, tabIndex } = this.state;
    const { selectedIndex, initialIndex } = this.props;

    // If new tabs
    if (tabs !== prevState.tabs) {
      if (this.categoryTab && this.categoryTab.wrappedInstance) {
        // If using back previous selected index and not the same state as local index
        const isCameFromBackButton = selectedIndex && selectedIndex !== tabIndex;
        // If this is the first time user enter compare view
        const isFirstTime = selectedIndex === null && initialIndex;

        if (isCameFromBackButton || isFirstTime) {
          const currIncomingIndex = isCameFromBackButton ? selectedIndex : initialIndex;
          // Check if previous selected index is still within range
          const tabIndexToUse = currIncomingIndex < tabs.length ? currIncomingIndex : 0;
          // Go to the tab and set new state;
          this.categoryTab.wrappedInstance.goToIndex(tabIndexToUse, true);
          this.setState({ tabIndex: tabIndexToUse });

          // Set index to store
          this.props.setSelectedIndex(tabIndexToUse);
          return;
        }

        // This happens when tabs are not the same when changing form within
        // compare view. It will try to go to the last selected plan type
        this.categoryTab.wrappedInstance.goToIndex(tabIndex);
      }
    }
  }

  /**
   * When clicked on main tabs
   *
   * @param {string} type - Selected plan type
   * @param {number} index - Tab index
   */
  handleTabClick(type, index) {
    // Set current plan type
    this.setState({
      currentTab: type,
    });

    this.props.setPlanType(type);
    this.props.setSelectedIndex(index);
  }

  /**
   * Render Main tabs
   *
   * @param {array} policyTypes - The avaliable policy types
   * @param {bool} isInitial - If is initial first time load
   */
  renderTabs(policyTypes = null, isInitial = false) {
    const { policyTypes: oldPolicyTypes, currentTab } = this.state;
    const { planImages } = this.props;

    // Use new policy types if avaliable
    const policyTypesToUse = policyTypes || oldPolicyTypes;

    // The tab index
    let index = 0;

    // Match possible policy types to default types,
    // with positioning in consideration
    const tabs = _.reduce(resultToMatch, (result, type) => {
      if (_.findIndex(policyTypesToUse, planType => planType === type) !== -1) {
        const theIndex = index;
        const image = planImages ? planImages[type] : null;
        // Set desc, image, and onClick for the tab
        result.push({
          desc: _.startCase(type),
          img: image,
          onClick: () => {
            this.handleTabClick(type, theIndex);
          },
        });
        index += 1;
      }
      return result;
    }, []);

    // Default currenTab name is from first item
    let tabInfo = { currentTab: _.lowerCase(tabs[0].desc) };

    // If tabs is changed while navigating in compare view
    if (!isInitial) {
      // Try to find if the old tab name still exists in new one
      const currentTabIndex = _.findIndex(tabs, tab => _.lowerCase(tab.desc) === currentTab);
      // Use 0 if no longer exists
      const tabIndexToUse = currentTabIndex !== -1 ? currentTabIndex : 0;
      // New tab
      const newTab = _.lowerCase(tabs[tabIndexToUse].desc);
      // Set new tab info
      tabInfo = {
        tabIndex: tabIndexToUse,
        currentTab: newTab,
      };

      // Set store data
      this.props.setPlanType(newTab);
      this.props.setSelectedIndex(tabIndexToUse);
    }

    // Return tabs
    const result = {
      ...tabInfo,
      tabs,
    };

    this.setState(result);

    return result;
  }

  /**
   * Render
   */
  render() {
    const {
      currentTab,
      tabs,
    } = this.state;

    const {
      data,
      scrollPopUpCallback,
      submitCallback,
      editForm,
      form,
      buttonDesc,
      forceFixedOnDesktop,
      category,
    } = this.props;

    // console.log(plansData);
    if (!data || !tabs || !currentTab) {
      return null;
    }

    const tabStyle = currentTab === 'default' ? 'inner-form-no-tabs' : '';

    return (
      <Fragment>
        <Row className={`inner-form-container ${tabStyle} inner-form-extra-padding`} noGutters>
          <TabView
            tabs={tabs}
            isDataFadeOnly
            isDisableDropdown
            big
            name={`${form}-tab`}
            ref={node => this.categoryTab = node}
          >
            {[<CompareView
              category={category}
              key="overview"
              className="overview-container-padding"
              notAccordion
              data={data}
              currentTab={currentTab}
              scrollPopUpCallback={scrollPopUpCallback}
              productChangable
              editForm={
                <ChangeInitialFormData
                  submitCallback={submitCallback}
                  form={form}
                  headerValue={editForm.headerValue}
                  valuesToListen={editForm.valuesToListen}
                  conditions={editForm.conditions}
                >
                  {editForm.editDetail}
                </ChangeInitialFormData>
              }
              buttonDesc={buttonDesc}
              forceFixedOnDesktop={forceFixedOnDesktop}
            />]}
          </TabView>
          <Enquiry />
        </Row>
      </Fragment>
    );
  }
}

MultiCompareInner.defaultProps = {
  initialIndex: 0,
  selectedIndex: null,
  data: null,
  planImages: null,
  buttonDesc: null,
  forceFixedOnDesktop: false,
  category: null,
};

MultiCompareInner.propTypes = {
  initialIndex: PropTypes.number,
  selectedIndex: PropTypes.number,
  setSelectedIndex: PropTypes.func.isRequired,
  setPlanType: PropTypes.func.isRequired,
  data: PropTypes.object,
  planImages: PropTypes.object,
  scrollPopUpCallback: PropTypes.func.isRequired,
  submitCallback: PropTypes.func.isRequired,
  editForm: PropTypes.object.isRequired,
  form: PropTypes.string.isRequired,
  buttonDesc: PropTypes.string,
  forceFixedOnDesktop: PropTypes.bool,
  category: PropTypes.string,
};

const mapStateToProps = state => ({
  selectedIndex: state.compare.selectedIndex,
});

const mapDispatchToProps = dispatch => ({
  setSelectedIndex: (index) => {
    dispatch(setSelectedIndex(index));
  },
  setPlanType: (type) => {
    dispatch(setPlanType(type));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(MultiCompareInner);
