import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import Endowment1 from 'assets/images/Endowment/policypal-endowment-insurance-1.svg';
import Endowment2 from 'assets/images/Endowment/policypal-endowment-insurance-2.svg';
import Endowment3 from 'assets/images/Endowment/policypal-endowment-insurance-3.svg';

import EndowmentEditDetail from './EndowmentEditDetail';
import FormLanding from '../Templates/FormLanding/FormLanding';

import './Endowment.scss';

class EndowmentMain extends Component {
  render() {
    const { endowmentFields, endowmentPromoGiveAway } = this.props;

    return (
      <div className="endowment-container">
        <FormLanding
          img1={Endowment1}
          img2={Endowment2}
          img3={Endowment3}
          imgAlt="PolicyPal Endowment Insurance"
          promo={endowmentPromoGiveAway}
          desc={
            <Fragment>
              <p>Get your Endowment Quotation</p>
              <p>in minutes!</p>
            </Fragment>
          }
        >
          <EndowmentEditDetail
            endowmentFields={endowmentFields}
          />
        </FormLanding>
      </div>
    );
  }
}

EndowmentMain.defaultProps = {
  endowmentPromoGiveAway: null,
};

EndowmentMain.propTypes = {
  endowmentFields: PropTypes.object.isRequired,
  endowmentPromoGiveAway: PropTypes.object,
};

export default EndowmentMain;
