/**
 * EndowmentEditDetail.js
 * The edit detail view for Endowment
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues, reset } from 'redux-form';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import Avatar from 'assets/images/MainFormIcons/Avatar.svg';
import Dollar from 'assets/images/MainFormIcons/Dollar.svg';
import Umbrella from 'assets/images/MainFormIcons/Umbrella.svg';

import Button from '../FormsV3/Button/Button';
import Selection from '../Forms/Selection/Selection';
import DatePickerV2 from '../Forms/DatePicker/DatePickerV2';
import Slider from '../Forms/Slider/Slider';
import DropDownV2 from '../Forms/DropDown/DropDownV2';

import RestrictionsPopup from '../Templates/RestrictionsPopup/RestrictionsPopup';

import { invokeSubmit } from '../../actions/editFormActions';
import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { setRestrictionPopup } from '../../actions/restrictionsActions';
import { validateForm, dateReValidate } from '../../lib/validators';
import { storeStringToDate, getAge, slashedStringDateToDate } from '../../lib/dateFunctions';
import { remapFormFields, simplifyCostArray } from '../../lib/helperFunctions';
import { gaEvent, ampTag } from '../../lib/tagFunctions';

const formName = 'endowment';

class EndowmentEditDetail extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { storeForm } = nextProps;
    if (storeForm && storeForm.birth_date !== prevState.birthDate) {
      return { birthDate: storeForm.birth_date };
    }

    return null;
  }

  constructor(props) {
    super(props);

    const { endowmentFields } = this.props;

    this.state = {
      errorStringData: {},
      birthDate: null,
      data: remapFormFields(endowmentFields.form_fields),
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCloseEdit = this.handleCloseEdit.bind(this);
    this.handleRevert = this.handleRevert.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  componentDidMount() {
    const { storeForm, setCloseCallBack } = this.props;
    this.originalValues = storeForm;

    if (setCloseCallBack) {
      setCloseCallBack(this.handleRevert);
    }
  }

  getAge(date) {
    let age = '';
    if (date && date !== -1) {
      age = getAge(date);
    }

    return age;
  }

  validateForm(e) {
    const { data } = this.state;

    const fieldsToCheck = [
      'gender',
      'smoker',
      'birth_date',
      'coverage_amount',
      'premium_term',
    ];

    const errorResult = validateForm(e, fieldsToCheck, {
      birth_date: { type: 'DATE', start: slashedStringDateToDate(data.birth_date.min_date), end: slashedStringDateToDate(data.birth_date.max_date) },
      smoker: { type: 'ARRAY', data: data.smoker.options },
      coverage_amount: { type: 'ARRAY', data: data.coverage_amount.options },
      premium_term: { type: 'ARRAY', data: data.premium_term.options },
    });

    this.setState({
      errorStringData: errorResult,
    });

    return errorResult;
  }

  handleSubmit(e, isFirstValidate = true) {
    // e.preventDefault();
    // console.log(e);
    const { birthDate } = this.state;
    const {
      closePopUp,
      setInsurFormState,
      setSubmit,
      storeForm,
      submitCallback,
      history,
    } = this.props;

    const errorResult = this.validateForm(isFirstValidate ? e : storeForm);
    const isEmptyResult = _.isEmpty(errorResult);

    if (dateReValidate(
      isEmptyResult,
      isFirstValidate,
      errorResult,
      error => this.setState({ errorStringData: error }),
      () => this.handleSubmit(storeForm, false),
    )) {
      return;
    }

    if (isEmptyResult) {
      setSubmit();
      this.originalValues = storeForm;

      if (submitCallback) {
        submitCallback();
      } else {
        gaEvent('Endowment - Get Quote', {
          event_category: 'Endowment',
          event_label: 'Button - Endowment - Get Quote',
        });

        const date = storeStringToDate(birthDate);
        const age = this.getAge(date);
        ampTag('EndGetQuoteA', {
          gender: e.gender,
          smoker: e.smoker,
          age,
          premium: e.coverage_amount,
          term: e.premium_term,
        });
      }

      setInsurFormState(FORMSTATES.CHOOSEPLAN.index);
      history.push(`/endowment${FORMSTATES.CHOOSEPLAN.path}`);


      if (closePopUp) {
        closePopUp();
      }
    }
  }

  handleRevert() {
    const { change, resetForm } = this.props;
    resetForm();
    Object.keys(this.originalValues).forEach((key) => {
      change(key, this.originalValues[key]);
    });
  }

  handleCloseEdit() {
    const { closePopUp } = this.props;
    closePopUp(this.handleRevert);
  }

  renderBirthDate() {
    const { errorStringData, birthDate, data } = this.state;
    const { form, change, storeForm } = this.props;

    const date = storeStringToDate(birthDate);
    const ageStyle = date ? 'active' : 'inactive';
    const age = this.getAge(date);

    return (
      <Col className="birth-date-container" xs={12} sm={10}>
        <p className="age">
          <span className={ageStyle}>Age</span>
          <span className="age-value">{age}</span>
        </p>
        <DatePickerV2
          form={form}
          change={change}
          name="birth_date"
          size="medium"
          zIndex={97}
          desc={data.birth_date.display_name}
          startDate={slashedStringDateToDate(data.birth_date.min_date)}
          endDate={slashedStringDateToDate(data.birth_date.max_date)}
          currDate={slashedStringDateToDate(data.birth_date.default_date)}
          errorString={errorStringData.birth_date}
          validateForm={() => this.validateForm(storeForm)}
        />
      </Col>
    );
  }

  renderFields() {
    const { errorStringData, data } = this.state;
    const { form, change, storeForm } = this.props;

    return (
      <Row noGutters>
        <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
          <img src={Avatar} alt="Avatar.svg" />
        </Col>
        <Col className="input-padding-right" xs={12} sm={5}>
          <Selection
            form={form}
            change={change}
            name="gender"
            type="text-selection"
            noEmptyTextSelection
            options={_.map(data.gender.options, option => ({
              key: option,
              description: option,
            }))}
            sizeInSM={6}
            desc={data.gender.display_name}
            zIndex={99}
            errorString={errorStringData.gender}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
        <Col className="input-padding-left" xs={12} sm={5}>
          <Selection
            form={form}
            change={change}
            name="smoker"
            type="text-selection"
            noEmptyTextSelection
            options={_.map(data.smoker.options, option => ({
              key: option,
              description: option,
            }))}
            sizeInSM={6}
            zIndex={98}
            desc={data.smoker.display_name}
            errorString={errorStringData.smoker}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
        <Col className="justify-content-center icon d-none d-sm-flex" sm={2} />
        {this.renderBirthDate()}
        <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
          <img style={{ marginTop: 32 }} src={Dollar} alt="Dollar.svg" />
        </Col>
        <Col xs={12} sm={10}>
          <Slider
            form={form}
            change={change}
            name="coverage_amount"
            desc={data.coverage_amount.display_name}
            zIndex={96}
            hideWidth={576}
            inputList={data.coverage_amount.options}
            displayList={simplifyCostArray(data.coverage_amount.options)}
            suffix="Annual Premium"
            errorString={errorStringData.coverage_amount}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
        <Col className="justify-content-center icon icon-car d-none d-sm-flex" sm={2}>
          <img src={Umbrella} alt="Umbrella.svg" />
        </Col>
        <Col className="input-padding-right" xs={12} sm={5}>
          <DropDownV2
            form={form}
            change={change}
            name="premium_term"
            placeholderString="Payment Term"
            dropList={data.premium_term.options}
            size="medium"
            zIndex={95}
            desc={data.premium_term.display_name}
            errorString={errorStringData.premium_term}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
      </Row>
    );
  }


  render() {
    const {
      handleSubmit,
      isPopUp,
      form,
    } = this.props;

    return (
      <div className="landing-form-container">
        {
          isPopUp ?
            <p className="edit-header">Edit Endowment Insurance Details</p>
          :
            null
        }
        <form onSubmit={handleSubmit(this.handleSubmit)}>
          <Row className="input-offset" noGutters>
            {this.renderFields()}
          </Row>
          <Row className="align-items-center button-container" noGutters>
            {
              isPopUp ?
                <Col className="justify-content-center button" xs={12}>
                  <Row noGutters>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        className="button-left"
                        variant="medium-flat"
                        width={165}
                        height={72}
                        onClick={() => this.handleCloseEdit()}
                      >
                        <p>Cancel</p>
                      </Button>
                    </Col>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        type="submit"
                        variant="medium-flat-green"
                        width={165}
                        height={72}
                      >
                        <p>Save</p>
                      </Button>
                    </Col>
                  </Row>
                </Col>
              :
                <Col className="justify-content-center button" xs={12}>
                  <Button type="submit" variant="medium-flat-green" width={165} height={72}>
                    <p>Get Quote</p>
                  </Button>
                </Col>
            }
          </Row>
        </form>
        <RestrictionsPopup ref={node => this.resPopup = node} />
      </div>
    );
  }
}

EndowmentEditDetail.defaultProps = {
  isPopUp: false,
  setCloseCallBack: null,
  storeForm: null,
  closePopUp: null,
  submitCallback: null,
};

EndowmentEditDetail.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  isPopUp: PropTypes.bool,
  resetForm: PropTypes.func.isRequired,
  closePopUp: PropTypes.func,
  setInsurFormState: PropTypes.func.isRequired,
  setSubmit: PropTypes.func.isRequired,
  storeForm: PropTypes.object,
  setCloseCallBack: PropTypes.func,
  submitCallback: PropTypes.func,
  endowmentFields: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  storeForm: getFormValues(formName)(state),
  isRevert: state.editForm.isRevert,
});

const mapDispatchToProps = dispatch => ({
  resetForm: () => {
    dispatch(reset(formName));
  },
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  setSubmit: () => {
    dispatch(invokeSubmit());
  },
  setRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
});

export default withRouter(reduxForm({
  form: formName,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(EndowmentEditDetail)));
