import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { parseEndowmentForm, getEndowmentPlans, getValidInsurersList, clearEndowmentData, setSelectedPlanData } from '../../actions/endowmentActions';

import EndowmentChoosePlan from '../../components/Endowment/EndowmentChoosePlan';
import { gaTag, ampTag } from '../../lib/tagFunctions';

class EndowmentChoosePlanContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.CHOOSEPLAN.index);
    gaTag({
      page_title: 'Endowment - Choose Plan',
      page_path: '/endowment/chooseplan',
    });
    ampTag('EndResultsS');
  }

  render() {
    const {
      endowmentForm,
      getPlans,
      getInsurersList,
      clearData,
      setFormState,
      plansData,
      validInsurersList,
      endowmentFields,
      history,
    } = this.props;

    return (
      <EndowmentChoosePlan
        mainForm={endowmentForm}
        endowmentFields={endowmentFields}
        parseForm={parseEndowmentForm}
        getPlans={getPlans}
        getInsurersList={getInsurersList}
        clearData={clearData}
        setSelectedPlanData={this.props.setSelectedPlanData}
        setFormState={() => {
          setFormState(FORMSTATES.DETAILFORM.index);
          history.push(`/endowment${FORMSTATES.DETAILFORM.path}`);
        }}
        loadingFailBackState={() => {
          history.goBack();
        }}
        plansData={plansData}
        validInsurersList={validInsurersList}
      />
    );
  }
}

EndowmentChoosePlanContainer.defaultProps = {
  plansData: null,
  validInsurersList: null,
};

EndowmentChoosePlanContainer.propTypes = {
  endowmentForm: PropTypes.object.isRequired,
  getPlans: PropTypes.func.isRequired,
  getInsurersList: PropTypes.func.isRequired,
  clearData: PropTypes.func.isRequired,
  setSelectedPlanData: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
  plansData: PropTypes.array,
  validInsurersList: PropTypes.array,
  endowmentFields: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  endowmentForm: getFormValues('endowment')(state),
  endowmentFields: state.endowment.endowmentFields,
  plansData: state.endowment.plansData,
  validInsurersList: state.endowment.validInsurersList,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  getPlans: (parsedForm) => {
    dispatch(getEndowmentPlans(parsedForm));
  },
  getInsurersList: (parsedForm) => {
    dispatch(getValidInsurersList(parsedForm));
  },
  clearData: () => {
    dispatch(clearEndowmentData());
  },
  setSelectedPlanData: (data) => {
    dispatch(setSelectedPlanData(data));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EndowmentChoosePlanContainer));
