import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Import Insurer Logos
import MultiCompareView from '../Templates/MultiCompareView/MultiCompareView';

import EndowmentEditDetail from './EndowmentEditDetail';

import { ampTag } from '../../lib/tagFunctions';

class EndowmentChoosePlan extends Component {
  getAmpTags(selectedPlanData) {
    try {
      return ({
        partner: selectedPlanData.info.insurer_slug,
      });
    } catch (error) {
      console.log(`Endowment choose plan Buy now Amp tag failed: ${error.message}`);
    }

    return {};
  }

  render() {
    const { endowmentFields } = this.props;

    return (
      <MultiCompareView
        {...this.props}
        category="endowment"
        form="endowment"
        editForm={{
          valuesToListen: [
            { value: 'gender', desc: 'Gender' },
            { value: 'smoker', desc: 'S.Status' },
            { value: 'birth_date', desc: 'Age' },
            { value: 'coverage_amount', desc: 'Sum', extra: 'cost_simplify' },
            { value: 'premium_term', desc: 'For' },
          ],
          editDetail: <EndowmentEditDetail endowmentFields={endowmentFields} isPopUp />,
        }}
        buttonDesc="Select"
        forceFixedOnDesktop
        trackingTags={{
          default: (selectedPlanData) => {
            ampTag('EndSelectA', this.getAmpTags(selectedPlanData, 'default'));
          },
        }}
      />
    );
  }
}

EndowmentChoosePlan.defaultProps = {
};

EndowmentChoosePlan.propTypes = {
  endowmentFields: PropTypes.object.isRequired,
};

export default EndowmentChoosePlan;
