/**
 * MultiCompareView.js
 * Multi Compare view with different insurers
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { parseData } from '../../../actions/insuranceActions';
import { setSelectedInsurers } from '../../../actions/compareActions';
import { setLoading, clearLoading } from '../../../actions/loadingScreenActions';
import { setRestrictionPopup } from '../../../actions/restrictionsActions';
import MultiCompareInner from './MultiCompareInner';

class MultiCompareView extends Component {
  /**
   * Lifecycle Method - getDerivedStateFromProps
   */
  static getDerivedStateFromProps(nextProps, prevState) {
    try {
      const {
        currentInsurers,
      } = prevState;

      const {
        plansData,
        validInsurersList,
        setFormState,
        loadingEnd,
        mainForm,
        setSelectedPlanData,
        trackingTags,
      } = nextProps;

      if (plansData && plansData[0] === 'INVALID') {
        return null;
      }

      // If plansData and validInsurersList are retrieved, parse data for display
      if (validInsurersList && plansData) {
        let newDefaultInsurers = currentInsurers || {};
        if (!currentInsurers && plansData) {
          newDefaultInsurers = _.map(plansData, plan => plan.info.insurer_slug);
          newDefaultInsurers = newDefaultInsurers.slice(0, 3);
          nextProps.setSelectedInsurers(newDefaultInsurers);
        }

        return {
          parsedData: parseData(
            plansData,
            validInsurersList,
            newDefaultInsurers,
            mainForm.trip_type,
            () => {
              setFormState();
            },
            setSelectedPlanData,
            loadingEnd,
            nextProps.setRestrictionPopup,
            null,
            trackingTags,
          ),
          currentInsurers: newDefaultInsurers,
        };
      }
    } catch (error) {
      return null;
    }

    return null;
  }

  /**
   * Lifecycle Method - constructor
   */
  constructor(props) {
    super(props);

    const {
      mainForm,
      parseForm,
      getPlans,
      getInsurersList,
      loading,
      defaultInsurers,
      plansData,
      validInsurersList,
      selectedInsurers,
      setSelectedPlanData,
      loadingEnd,
      setFormState,
      loadingFailBackState,
      clearData,
      trackingTags,
    } = this.props;

    try {
      // If fresh init
      if (!plansData || !validInsurersList) {
        // Initally, get the params from mainform and default insurers
        const parsedParams = parseForm(mainForm, defaultInsurers);

        // Set loading
        loading(() => {
          clearData();
          loadingFailBackState();
        }, 'No available plans found.', 30000, '');

        // Get plans and valid insurers list
        getPlans(parsedParams);
        getInsurersList(parsedParams);

        // Set initial default insurers
        this.props.setSelectedInsurers(defaultInsurers);

        // currentInsurers: Current selected list of insurers
        // parsedData: The parsed data to display
        // hoveredIndex: The current hovered index on the insurer selection dropdown, default is 0
        this.state = {
          currentInsurers: defaultInsurers,
          parsedData: null,
        };
      } else {
        // Set existing selected insurers and get old parsed data
        // (Will reach this condition when using the back button)
        this.state = {
          currentInsurers: selectedInsurers,
          parsedData: parseData(
            plansData,
            validInsurersList,
            selectedInsurers,
            mainForm.trip_type,
            () => setFormState(),
            setSelectedPlanData,
            loadingEnd,
            this.props.setRestrictionPopup,
            null,
            trackingTags,
          ),
        };
      }
    } catch (error) {
      // eslint-disable-next-line
    }

    this.getNewData = this.getNewData.bind(this);
    this.onInsurerClick = this.onInsurerClick.bind(this);
  }

  componentDidUpdate() {
    const {
      plansData,
      loadingEnd,
      loading,
      clearData,
      loadingFailBackState,
    } = this.props;

    if (plansData && plansData[0] === 'INVALID') {
      loadingEnd();
      if (!this.isLoading) {
        this.isLoading = true;
        setTimeout(() =>
          loading(() => {
            clearData();
            loadingFailBackState();
            this.isLoading = false;
          }, 'No available plans found.', 50, ''), 500);
      }
    }
  }
  /**
   * Lifecycle Method - shouldComponentUpdate
   */
  /* shouldComponentUpdate(nextProps, nextState) {
    const { parsedData } = this.state;

    // Render only is new or policyTypes changed
    return !(parsedData && nextState.parsedData) ||
          !_.isEqual(parsedData.policyTypes, nextState.parsedData.policyTypes);
  } */

  /**
   * Handle selection on insurer list
   *
   * @param {object} data - The insurer list item data
   */
  onInsurerClick(data, hoveredIndex) {
    // parsedData: The parsed data to display
    const { parsedData } = this.state;

    // Remap current insurers as sometimes when change form data, the insurers
    // can be less than 3.
    // console.log(parsedData.selectedInsurers, hoveredIndex);
    const currentInsurers = _.reduce(parsedData.selectedInsurers, (result, insurer) => {
      if (insurer) {
        result.push(insurer.insurerSlug);
      }
      return result;
    }, []);

    // Change insurer of current selected position
    currentInsurers[hoveredIndex] = data.insurer_slug;

    // Set current insurers
    this.setState({
      currentInsurers,
    });

    // Set current insurers in store
    this.props.setSelectedInsurers(currentInsurers);

    // Get new compare data based on the new selected insurers
    this.getNewData(currentInsurers);
  }

  /**
   * Get new compare data.
   * Used when changing insurers or changing form fields.
   *
   * @param {array} incomingInsurers - selected insurers
   */
  getNewData(incomingInsurers = null) {
    const { currentInsurers } = this.state;
    const {
      loading,
      getPlans,
      getInsurersList,
      clearData,
      mainForm,
      parseForm,
      loadingFailBackState,
    } = this.props;

    // If init, use default, else the current array
    const insurerToUse = incomingInsurers || currentInsurers;
    // Parse the form for params
    const parsedParams = parseForm(mainForm, insurerToUse);
    // Clear data in store first, so it will detect as fresh props from store
    clearData();
    // Set loading screen
    loading(() => {
      clearData();
      loadingFailBackState();
    }, 'No available plans found.', 30000, '');
    // Get plans and insurer lists
    getPlans(parsedParams);
    getInsurersList(parsedParams);
  }

  /**
   * Render
   */
  render() {
    try {
      const { parsedData } = this.state;
      const {
        editForm,
        form,
        planImages,
        initialIndex,
        buttonDesc,
        forceFixedOnDesktop,
        category,
      } = this.props;

      // Do not render if no data
      if (!parsedData) {
        return null;
      }

      // console.log(parsedData);
      return (
        <MultiCompareInner
          category={category}
          initialIndex={initialIndex}
          planImages={planImages}
          data={parsedData}
          form={form}
          editForm={editForm}
          scrollPopUpCallback={this.onInsurerClick}
          submitCallback={this.getNewData}
          buttonDesc={buttonDesc}
          forceFixedOnDesktop={forceFixedOnDesktop}
        />
      );
    } catch (error) {
      return null;
    }
  }
}

MultiCompareView.defaultProps = {
  initialIndex: 0,
  plansData: null,
  validInsurersList: null,
  selectedInsurers: null,
  trackingTags: null,
  planImages: null,
  defaultInsurers: null,
  buttonDesc: null,
  forceFixedOnDesktop: false,
  category: null,
};

MultiCompareView.propTypes = {
  setSelectedInsurers: PropTypes.func.isRequired,
  mainForm: PropTypes.object.isRequired,
  parseForm: PropTypes.func.isRequired,
  getPlans: PropTypes.func.isRequired,
  getInsurersList: PropTypes.func.isRequired,
  clearData: PropTypes.func.isRequired,
  loading: PropTypes.func.isRequired,
  defaultInsurers: PropTypes.array,
  editForm: PropTypes.object.isRequired,
  form: PropTypes.string.isRequired,
  planImages: PropTypes.object,
  initialIndex: PropTypes.number,
  plansData: PropTypes.array,
  validInsurersList: PropTypes.array,
  selectedInsurers: PropTypes.array,
  setSelectedPlanData: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
  setFormState: PropTypes.func.isRequired,
  setRestrictionPopup: PropTypes.func.isRequired,
  loadingFailBackState: PropTypes.func.isRequired,
  trackingTags: PropTypes.object,
  buttonDesc: PropTypes.string,
  forceFixedOnDesktop: PropTypes.bool,
  category: PropTypes.string,
};

const mapStateToProps = state => ({
  selectedInsurers: state.compare.selectedInsurers,
});

const mapDispatchToProps = dispatch => ({
  loading: (callBack, errorMsg, time, mainMsg) => {
    dispatch(setLoading(callBack, errorMsg, time, mainMsg));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
  setRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
  setSelectedInsurers: (array) => {
    dispatch(setSelectedInsurers(array));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(MultiCompareView);
